*
{
  margin: 0;
  padding: 0;
  font-family: 'Times New Roman', Times, serif;
}
body
{
  background-image: url("https://c8.alamy.com/comp/H7KWHW/empty-interior-of-living-room-with-plant-over-brick-wall-background-H7KWHW.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.heading
{
  text-align: center;
  background-color: rgba(125, 28, 11, 0.425);
  border-radius: 100px 10px 100px 10px;
  width: 220px;
  font-size: 25px;
  font-weight: 700;
  margin-left: 200px;
  margin-bottom: 5px;
}
.formdesign
{
  background-color: transparent;
  padding: 10px 20px;
  border-radius: 5px;
  width: 600px;
  height: 600px;
}
label
{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:center;
  font-size: 20px;
  font-weight: 800;
  color: rgb(4, 4, 4);
  margin-top: 6px;
  margin-bottom:5px ;
}
input
{
  display: block;
  margin-left: 5px;
  width: 500px;
  height: 25px;
  margin-top: 5px;
  border-color: rgb(11, 169, 169);
  background-color: transparent;
}
textarea
{
  display: block;
  margin-left: 5px;
  width: 500px;
  height: 35px;
  margin-top: 5px;
  border-color:10px solid rgba(11, 169, 169);
  background-color: transparent;
}
.button
{
  width: 100px;
  height: 35px;
  margin-left: 250px;
  margin-top: 3px;
  background-color: rgb(104, 244, 102);
  color: rgb(8, 8, 8);
  font-size: 20px;
  font-weight: 700;
  border-color: whitesmoke;
  border-radius: 5px;
}